import React, { Component } from 'react';
import axios from 'axios';

class ContactForm extends Component {

    handleSubmit(e) {
        e.preventDefault();
        const name = document.getElementById('name').value;
        const email = document.getElementById('email').value;
        const message = document.getElementById('message').value;
        axios({
            method: "POST",
            url: "http://45.44.252.19:3002/send",
            data: {
                name: name,
                email: email,
                message: message
            }
        }).then((response) => {
            if (response.data.msg === 'success') {
                alert("Message Sent.");
                this.resetForm()
            } else if (response.data.msg === 'fail') {
                alert("Message failed to send.")
            }
        })
    }

    resetForm() {
        document.getElementById('contact-form').reset();
    }

    render() {
        return (
            <div className="row">
                <div className="twelve columns">
                    <div className="col-sm-4 offset-sm-4">
                        <form id="contact-form" name="sentMessage" novalidate="novalidate" onSubmit={this.handleSubmit.bind(this)} method="POST">
                            <div className="form-group">
                                <label for="name">Nom</label>
                                <input type="text" className="form-control" id="name" />
                            </div>
                            <div className="form-group">
                                <label for="exampleInputEmail1">Adresse e-mail</label>
                                <input type="email" className="form-control" id="email" aria-describedby="emailHelp" />
                            </div>
                            <div className="form-group">
                                <label for="message">Message</label>
                                <textarea className="form-control" rows="5" id="message"></textarea>
                            </div>
                            <br />
                            <div id="success"></div>
                            <div class="form-group">
                            <button type="submit" className="btn btn-submit btn-xl" id="sendMessageButton">Envoyer</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default ContactForm; 